import React from 'react'
import Image1 from '../../images/noodles.png'
import Image2 from '../../images/sandwich.png'
import Image3 from '../../images/waffle.png'
import Image4 from '../../images/corn.png'
import { MenuContainer, MenuH1, MenuH2, MenuWrapper, MenuIcon, MenuP, MenuCard, MenuCart } from './MenuElements'

const Menu = () => {
    return (
        <MenuContainer id='discover'>
            <MenuH1>Menu of the Week</MenuH1>
            <MenuWrapper>
                <MenuCard>
                    <MenuIcon src={Image1} />
                    <MenuH2>Noodles</MenuH2>
                    <MenuP>Price : ₹150</MenuP>
                    <MenuCart />
                </MenuCard>
                <MenuCard>
                    <MenuIcon src={Image2} />
                    <MenuH2>Sandwich</MenuH2>
                    <MenuP>Price : ₹350</MenuP>
                    <MenuCart />
                </MenuCard>
                <MenuCard>
                    <MenuIcon src={Image3} />
                    <MenuH2>Waffles</MenuH2>
                    <MenuP>Price : ₹100</MenuP>
                    <MenuCart />
                </MenuCard>
                <MenuCard>
                    <MenuIcon src={Image4} />
                    <MenuH2>Corn Sabji</MenuH2>
                    <MenuP>Price : ₹250</MenuP>
                    <MenuCart />
                </MenuCard>
            </MenuWrapper>
        </MenuContainer>
    )
}

export default Menu
